	if (!localStorage.getItem("cmrProjects")) {
		localStorage.setItem("cmrProjects", JSON.stringify(projects));
	}

	const fetchAll = () =>
		new Promise((resolve) => {
			window.setTimeout(function () {
				resolve(JSON.parse(localStorage.getItem("cmrProjects")));
			}, 1000);
		});

	const update = (id, data) =>
		new Promise((resolve) => {
			const items = JSON.parse(localStorage.getItem("cmrProjects"));
			const index = items.findIndex((i) => i.id === id);
			items[index] = { ...items[index], ...data };
			localStorage.setItem("cmrProjects", JSON.stringify(items));
			resolve(items);
		});
	const add = (item) =>
		new Promise((resolve) => {
			const items = JSON.parse(localStorage.getItem("cmrProjects"));
			const updatedItems = [...items, item];
			localStorage.setItem("cmrProjects", JSON.stringify(updatedItems));
			resolve(updatedItems);
		});

	const deleteItem = (id) =>
		new Promise((resolve) => {
			const items = JSON.parse(localStorage.getItem("cmrProjects"));
			const updatedItems = items.filter((i) => i.id !==id);
			localStorage.setItem("cmrProjects", JSON.stringify(updatedItems));
			resolve(updatedItems);
		});

// eslint-disable-next-line import/no-anonymous-default-export
	export default {
		fetchAll,
		update,
		add,
		deleteItem
	};
